//  DTO
export type PostDto = {
  id: number;
  image: string | null; //     null
  text: string;
  date: string; // ISO date
  lesson_num: number;
  title: string;
  description: string;
  author: number;
};

//   Post ( ,    )
export type Post = {
  id: number;
  image?: string;
  text: string;
  date: string;
  lesson_num: number;
  title: string;
  author: number | string;
  likes: number;
  dislikes: number;
};

export function fromDto(dto: PostDto): Post {
  return {
    id: dto.id,
    image: dto.image || undefined,
    title: dto.title,
    text: dto.description || dto.text,
    date: dto.date,
    lesson_num: dto.lesson_num,
    author: dto.author,
    likes: Math.floor(Math.random() * 200) + 10, //     
    dislikes: Math.floor(Math.random() * 20) + 1, //     
  };
}
